
import { AppBar, Toolbar, styled } from "@mui/material"

const Container = styled(AppBar)`
    background: #060606;
    position: static;
    border-bottom: 1px solid #2f2f2f;
    height: 9vh;
`;

const Header = () => {
    const logo = 'data:image/png;base64,';
    return (
        <Container>
            <Toolbar>
                <img src={logo} alt="logo" style={{ width: 40 }} />
            </Toolbar>
        </Container>
    )
}

export default Header
